import axios from 'axios';

const API_URL = '/api/technicians/';

// Obter tcnicos prximos
const getNearbyTechnicians = async (location, token) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`
    }
  };

  const response = await axios.get(
    `${API_URL}nearby?lat=${location.lat}&lng=${location.lng}`,
    config
  );

  return response.data;
};

// Obter tcnico por ID
const getTechnician = async (technicianId, token) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`
    }
  };

  const response = await axios.get(API_URL + technicianId, config);

  return response.data;
};

// Novo: Obter top tcnicos por regio
const getTopTechniciansByRegion = async ({ city, state, limit = 5 }, token) => {
  const config = token ? {
    headers: {
      Authorization: `Bearer ${token}`
    }
  } : {};
  
  const params = new URLSearchParams();
  if (city) params.append('city', city);
  if (state) params.append('state', state);
  if (limit) params.append('limit', limit);
  
  const response = await axios.get(`${API_URL}top?${params.toString()}`, config);
  return response.data;
};

// Novo: Enviar avaliao do tcnico
const addTechnicianReview = async (technicianId, { ticketId, rating, comment }, token) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`,
      'Content-Type': 'application/json'
    }
  };
  const response = await axios.post(`${API_URL}${technicianId}/reviews`, { ticketId, rating, comment }, config);
  return response.data;
};

const technicianService = {
  getNearbyTechnicians,
  getTechnician,
  getTopTechniciansByRegion,
  addTechnicianReview
};

export default technicianService;