import React from 'react';
import { Chip } from '@mui/material';

// Componente para exibir o status do chamado com cores diferentes
function TicketStatusBadge({ status }) {
  // Definio das cores e labels para cada status
  const statusConfig = {
    novo: {
      color: 'info',
      label: 'Novo'
    },
    em_andamento: {
      color: 'warning',
      label: 'Em Andamento'
    },
    aguardando_aprovao: {
      color: 'secondary',
      label: 'Aguardando Aprovao'
    },
    aprovado: {
      color: 'success',
      label: 'Aprovado'
    },
    reprovado: {
      color: 'error',
      label: 'Reprovado'
    },
    concludo: {
      color: 'success',
      label: 'Concludo'
    },
    cancelado: {
      color: 'error',
      label: 'Cancelado'
    },
    default: {
      color: 'default',
      label: 'Desconhecido'
    }
  };

  // Obter configurao do status ou usar default se no existir
  const config = statusConfig[status] || statusConfig.default;

  return (
    <Chip 
      label={config.label} 
      color={config.color} 
      size="small" 
      variant="filled"
      className="status-badge"
    />
  );
}

export default TicketStatusBadge;