import axios from 'axios';

const API_URL = '/api/ads';

// Obter anncios ativos para o usurio atual (usa token)
const getActiveAds = async (token) => {
  const res = await axios.get(API_URL, {
    headers: { Authorization: `Bearer ${token}` },
  });
  return res.data;
};

// Obter anncios pblicos (sem token)
const getPublicAds = async () => {
  const res = await axios.get(`${API_URL}/public`);
  return res.data;
};

// Criar anncio (tcnico autenticado)
const createAd = async ({ token, payload }) => {
  const res = await axios.post(API_URL, payload, {
    headers: { Authorization: `Bearer ${token}` },
  });
  return res.data;
};

// Listar anncios do tcnico autenticado
const getMyAds = async (token) => {
  const res = await axios.get(`${API_URL}/mine`, {
    headers: { Authorization: `Bearer ${token}` },
  });
  return res.data;
};

// Atualizar anncio do tcnico
const updateAd = async ({ token, id, payload }) => {
  const res = await axios.put(`${API_URL}/${id}`, payload, {
    headers: { Authorization: `Bearer ${token}` },
  });
  return res.data;
};

// Upload de mdia (retorna filePath)
const uploadMedia = async ({ token, file }) => {
  const formData = new FormData();
  formData.append('file', file);
  const res = await axios.post('/api/uploads', formData, {
    headers: {
      Authorization: `Bearer ${token}`,
      'Content-Type': 'multipart/form-data',
    },
  });
  return res.data; // { message, filePath }
};

// Comprar remoo de anncios (cliente)
const purchaseAdRemoval = async ({ months = 1, token }) => {
  const res = await axios.post(`${API_URL}/purchase-remove`, { months }, {
    headers: { Authorization: `Bearer ${token}` },
  });
  return res.data; // { message, adFreeUntil, amountCharged, currency }
};

const adsService = {
  getActiveAds,
  getPublicAds,
  createAd,
  getMyAds,
  updateAd,
  uploadMedia,
  purchaseAdRemoval,
};

export default adsService;