import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { toast } from 'react-toastify';

// Material UI
import {
  Box,
  Button,
  Card,
  CardContent,
  Container,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Divider,
  FormControl,
  FormControlLabel,
  Grid,
  IconButton,
  InputAdornment,
  InputLabel,
  MenuItem,
  Paper,
  Select,
  Switch,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  Typography,
} from '@mui/material';
import {
  Add as AddIcon,
  Delete as DeleteIcon,
  Edit as EditIcon,
  Save as SaveIcon,
} from '@mui/icons-material';

// Componentes
import Sidebar from '../../components/Sidebar';

// Dados simulados (sero substitudos pela API)
const serviceCategories = [
  'Manuteno de Computadores',
  'Manuteno de Notebooks',
  'Manuteno de Impressoras',
  'Redes e Conectividade',
  'Instalao de Software',
  'Remoo de Vrus e Malware',
  'Recuperao de Dados',
  'Consultoria em TI',
  'Suporte Remoto',
  'Manuteno de Celulares',
  'Manuteno de Videogames',
  'Outros',
];

const initialServices = [
  {
    id: '1',
    name: 'Formatao de Computador',
    description: 'Formatao completa com backup de dados e instalao do sistema operacional.',
    category: 'Manuteno de Computadores',
    price: 120,
    estimatedTime: '2 horas',
    isActive: true,
  },
  {
    id: '2',
    name: 'Limpeza de Notebook',
    description: 'Limpeza interna e externa com troca de pasta trmica.',
    category: 'Manuteno de Notebooks',
    price: 100,
    estimatedTime: '1 hora',
    isActive: true,
  },
  {
    id: '3',
    name: 'Instalao de Programas',
    description: 'Instalao e configurao de programas bsicos e especficos.',
    category: 'Instalao de Software',
    price: 80,
    estimatedTime: '1 hora',
    isActive: true,
  },
  {
    id: '4',
    name: 'Configurao de Rede Wi-Fi',
    description: 'Configurao de roteadores e dispositivos para conexo em rede.',
    category: 'Redes e Conectividade',
    price: 90,
    estimatedTime: '1 hora',
    isActive: true,
  },
  {
    id: '5',
    name: 'Recuperao de Arquivos Deletados',
    description: 'Recuperao de arquivos deletados acidentalmente ou por falha no sistema.',
    category: 'Recuperao de Dados',
    price: 150,
    estimatedTime: '3 horas',
    isActive: false,
  },
];

function Services() {
  const [services, setServices] = useState([]);
  const [loading, setLoading] = useState(true);
  const [openDialog, setOpenDialog] = useState(false);
  const [openDeleteDialog, setOpenDeleteDialog] = useState(false);
  const [currentService, setCurrentService] = useState({
    id: '',
    name: '',
    description: '',
    category: '',
    price: '',
    estimatedTime: '',
    isActive: true,
  });
  const [editMode, setEditMode] = useState(false);
  const [serviceToDelete, setServiceToDelete] = useState(null);

  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.auth);

  useEffect(() => {
    // Aqui seria implementada a chamada  API para buscar os servios do tcnico
    // dispatch(getTechnicianServices());
    
    // Simulando carregamento
    setLoading(true);
    setTimeout(() => {
      setServices(initialServices);
      setLoading(false);
    }, 1000);
  }, [dispatch]);

  const handleOpenDialog = (service = null) => {
    if (service) {
      setCurrentService(service);
      setEditMode(true);
    } else {
      setCurrentService({
        id: '',
        name: '',
        description: '',
        category: '',
        price: '',
        estimatedTime: '',
        isActive: true,
      });
      setEditMode(false);
    }
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
  };

  const handleOpenDeleteDialog = (service) => {
    setServiceToDelete(service);
    setOpenDeleteDialog(true);
  };

  const handleCloseDeleteDialog = () => {
    setOpenDeleteDialog(false);
    setServiceToDelete(null);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setCurrentService({
      ...currentService,
      [name]: value,
    });
  };

  const handleSwitchChange = (e) => {
    setCurrentService({
      ...currentService,
      isActive: e.target.checked,
    });
  };

  const handleSubmit = () => {
    // Validao bsica
    if (!currentService.name.trim()) {
      toast.error('O nome do servio  obrigatrio');
      return;
    }
    if (!currentService.category) {
      toast.error('A categoria  obrigatria');
      return;
    }
    if (!currentService.price || isNaN(currentService.price) || currentService.price <= 0) {
      toast.error('O preo deve ser um valor positivo');
      return;
    }

    if (editMode) {
      // Aqui seria implementada a chamada  API para atualizar o servio
      // dispatch(updateService(currentService));
      
      // Simulando atualizao
      const updatedServices = services.map((service) =>
        service.id === currentService.id ? currentService : service
      );
      setServices(updatedServices);
      toast.success('Servio atualizado com sucesso!');
    } else {
      // Aqui seria implementada a chamada  API para criar o servio
      // dispatch(createService(currentService));
      
      // Simulando criao
      const newService = {
        ...currentService,
        id: Date.now().toString(),
      };
      setServices([...services, newService]);
      toast.success('Servio criado com sucesso!');
    }

    handleCloseDialog();
  };

  const handleDelete = () => {
    if (!serviceToDelete) return;

    // Aqui seria implementada a chamada  API para excluir o servio
    // dispatch(deleteService(serviceToDelete.id));
    
    // Simulando excluso
    const updatedServices = services.filter((service) => service.id !== serviceToDelete.id);
    setServices(updatedServices);
    toast.success('Servio excludo com sucesso!');
    handleCloseDeleteDialog();
  };

  const handleToggleActive = (service) => {
    const updatedService = { ...service, isActive: !service.isActive };
    
    // Aqui seria implementada a chamada  API para atualizar o status do servio
    // dispatch(updateServiceStatus(service.id, !service.isActive));
    
    // Simulando atualizao
    const updatedServices = services.map((s) =>
      s.id === service.id ? updatedService : s
    );
    setServices(updatedServices);
    toast.success(`Servio ${updatedService.isActive ? 'ativado' : 'desativado'} com sucesso!`);
  };

  return (
    <Box sx={{ display: 'flex' }}>
      <Sidebar />
      <Box component="main" sx={{ flexGrow: 1, p: 3 }}>
        <Container maxWidth="lg">
          <Box sx={{ mb: 4 }}>
            <Typography variant="h4" component="h1" gutterBottom>
              Meus Servios
            </Typography>
            <Typography variant="body1" color="textSecondary" paragraph>
              Gerencie os servios que voc oferece aos clientes. Defina preos, descries e disponibilidade.
            </Typography>
          </Box>

          <Paper sx={{ mb: 3, p: 2 }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
              <Typography variant="h6">
                Lista de Servios
              </Typography>
              <Button
                variant="contained"
                color="primary"
                startIcon={<AddIcon />}
                onClick={() => handleOpenDialog()}
              >
                Novo Servio
              </Button>
            </Box>

            <TableContainer>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Nome</TableCell>
                    <TableCell>Categoria</TableCell>
                    <TableCell>Preo</TableCell>
                    <TableCell>Tempo Estimado</TableCell>
                    <TableCell>Status</TableCell>
                    <TableCell align="right">Aes</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {services.map((service) => (
                    <TableRow key={service.id}>
                      <TableCell>{service.name}</TableCell>
                      <TableCell>{service.category}</TableCell>
                      <TableCell>R$ {service.price.toFixed(2)}</TableCell>
                      <TableCell>{service.estimatedTime}</TableCell>
                      <TableCell>
                        <FormControlLabel
                          control={
                            <Switch
                              checked={service.isActive}
                              onChange={() => handleToggleActive(service)}
                              color="primary"
                            />
                          }
                          label={service.isActive ? 'Ativo' : 'Inativo'}
                        />
                      </TableCell>
                      <TableCell align="right">
                        <IconButton
                          color="primary"
                          onClick={() => handleOpenDialog(service)}
                          size="small"
                        >
                          <EditIcon />
                        </IconButton>
                        <IconButton
                          color="error"
                          onClick={() => handleOpenDeleteDialog(service)}
                          size="small"
                        >
                          <DeleteIcon />
                        </IconButton>
                      </TableCell>
                    </TableRow>
                  ))}

                  {services.length === 0 && (
                    <TableRow>
                      <TableCell colSpan={6} align="center">
                        {loading ? 'Carregando...' : 'Nenhum servio cadastrado'}
                      </TableCell>
                    </TableRow>
                  )}
                </TableBody>
              </Table>
            </TableContainer>
          </Paper>

          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <Card>
                <CardContent>
                  <Typography variant="h6" gutterBottom>
                    Dicas para Definir Preos
                  </Typography>
                  <Divider sx={{ mb: 2 }} />
                  <Typography variant="body2" paragraph>
                    1. Pesquise os preos praticados por outros tcnicos na sua regio.
                  </Typography>
                  <Typography variant="body2" paragraph>
                    2. Considere o tempo mdio gasto em cada servio e sua complexidade.
                  </Typography>
                  <Typography variant="body2" paragraph>
                    3. Inclua no preo os custos de deslocamento, quando aplicvel.
                  </Typography>
                  <Typography variant="body2" paragraph>
                    4. Oferea pacotes de servios com desconto para atrair mais clientes.
                  </Typography>
                  <Typography variant="body2">
                    5. Revise seus preos periodicamente com base na demanda e feedback dos clientes.
                  </Typography>
                </CardContent>
              </Card>
            </Grid>

            <Grid item xs={12} md={6}>
              <Card>
                <CardContent>
                  <Typography variant="h6" gutterBottom>
                    Estatsticas de Servios
                  </Typography>
                  <Divider sx={{ mb: 2 }} />
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                    <Typography variant="body2">Total de Servios:</Typography>
                    <Typography variant="body2" fontWeight="bold">{services.length}</Typography>
                  </Box>
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                    <Typography variant="body2">Servios Ativos:</Typography>
                    <Typography variant="body2" fontWeight="bold">
                      {services.filter((s) => s.isActive).length}
                    </Typography>
                  </Box>
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                    <Typography variant="body2">Servios Inativos:</Typography>
                    <Typography variant="body2" fontWeight="bold">
                      {services.filter((s) => !s.isActive).length}
                    </Typography>
                  </Box>
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                    <Typography variant="body2">Preo Mdio:</Typography>
                    <Typography variant="body2" fontWeight="bold">
                      {services.length > 0
                        ? `R$ ${(services.reduce((sum, s) => sum + Number(s.price), 0) / services.length).toFixed(2)}`
                        : 'N/A'}
                    </Typography>
                  </Box>
                </CardContent>
              </Card>
            </Grid>
          </Grid>

          {/* Dialog para Adicionar/Editar Servio */}
          <Dialog open={openDialog} onClose={handleCloseDialog} maxWidth="sm" fullWidth>
            <DialogTitle>
              {editMode ? 'Editar Servio' : 'Novo Servio'}
            </DialogTitle>
            <DialogContent>
              <Grid container spacing={2} sx={{ mt: 1 }}>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="Nome do Servio"
                    name="name"
                    value={currentService.name}
                    onChange={handleChange}
                    required
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="Descrio"
                    name="description"
                    value={currentService.description}
                    onChange={handleChange}
                    multiline
                    rows={3}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <FormControl fullWidth>
                    <InputLabel>Categoria</InputLabel>
                    <Select
                      name="category"
                      value={currentService.category}
                      onChange={handleChange}
                      label="Categoria"
                      required
                    >
                      {serviceCategories.map((category) => (
                        <MenuItem key={category} value={category}>
                          {category}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="Preo"
                    name="price"
                    type="number"
                    value={currentService.price}
                    onChange={handleChange}
                    InputProps={{
                      startAdornment: <InputAdornment position="start">R$</InputAdornment>,
                    }}
                    required
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    fullWidth
                    label="Tempo Estimado"
                    name="estimatedTime"
                    value={currentService.estimatedTime}
                    onChange={handleChange}
                    placeholder="Ex: 1 hora, 30 minutos"
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <FormControlLabel
                    control={
                      <Switch
                        checked={currentService.isActive}
                        onChange={handleSwitchChange}
                        color="primary"
                      />
                    }
                    label="Servio Ativo"
                  />
                </Grid>
              </Grid>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleCloseDialog} color="inherit">
                Cancelar
              </Button>
              <Button
                onClick={handleSubmit}
                color="primary"
                variant="contained"
                startIcon={<SaveIcon />}
              >
                Salvar
              </Button>
            </DialogActions>
          </Dialog>

          {/* Dialog para Confirmar Excluso */}
          <Dialog open={openDeleteDialog} onClose={handleCloseDeleteDialog}>
            <DialogTitle>Confirmar Excluso</DialogTitle>
            <DialogContent>
              <DialogContentText>
                Tem certeza que deseja excluir o servio "{serviceToDelete?.name}"? Esta ao no pode ser desfeita.
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleCloseDeleteDialog} color="inherit">
                Cancelar
              </Button>
              <Button onClick={handleDelete} color="error" variant="contained">
                Excluir
              </Button>
            </DialogActions>
          </Dialog>
        </Container>
      </Box>
    </Box>
  );
}

export default Services;